(function() {
  'use strict';

  angular
    .module('angularFloatingPoint', ['floatingPoint']);

})();

(function() {
  'use strict';

  angular
    .module('angularFloatingPoint')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function () {
  'use strict';

  angular
    .module('angularFloatingPoint')
    .controller('IndexController', function ($scope,floatingPoint) {

      $scope.calculate = function () {
        $scope.result =$scope.num0*$scope.num1;
        $scope.prettyResult=floatingPoint.makePretty($scope.result)
      }

      $scope.num0=6.1;
      $scope.num1=6;
      $scope.calculate();

    });
})();

(function () {
  'use strict';

  angular.module('floatingPoint',[])
    .service('floatingPoint', function () {

      this.makePretty=function (value) {
        return parseFloat(value).toPrecision(15)*1.0;
      }
    });

})();
